@import url(https://fonts.googleapis.com/css?family=Advent+Pro:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",'Advent Pro',
     sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a:hover {
  color: white !important;
}
/* Top-level shared styles */
/* You can keep styles that are shared by different components here */

html {
  height: 100%;
}

body {
  color: darkslategrey;
  background-color: white;
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, sans-serif;
  height: 100vh;
}

/* Styles for the Header React component */
.header {
  margin-bottom: 40px;
}

.header-profile {
  display: inline-block;
  width: 70%;
}

.headerSearch {
  display: inline-block;
  right: 0;
  width: 30%;
}

@media only screen and (max-width: 600px) {
  .header-profile {
    width: 20%;
  }
  .headerSearch {
    width: 80%;
  }
}
.MuiGrid-item.profile-username {
    display: none;
    margin: 0 10px;
  }

.MuiSvgIcon-root.profile-status {
    display: none;
    margin-top: 4px;
}

@media (min-width: 785px) {
    .MuiGrid-item.profile-username {
        display: block;
        margin: 0 10px;
      }
    
    .MuiSvgIcon-root.profile-status {
        display: block;
        margin-top: 2px;
    }
}
